  dummy(id: string): Gear.Gear {
    let gear = new Gear.Gear(id);
    gear.type = Gear.Type.Weapon;
    gear.set = Gear.Set.Speed;
    gear.grade = Gear.Grade.EPIC;
    gear.level = 85;
    gear.enhance = 15;
    gear.main = Gear.Stat.ATK;
    gear.hpp = 1;
    gear.hp = 20000;
    gear.defp = 3;
    gear.def = 2000;
    gear.atkp = 5;
    gear.atk = 4000;
    gear.cri = 100;
    gear.cdmg = 350;
    gear.spd = 9;
    gear.eff = 10;
    gear.res = 11;
    gear.score = 12;
    return gear;
